<template>
  <el-card class="box-card">
    <el-form :inline="true" class="demo-form-inline">
      <el-form-item label="username">
        <el-input placeholder="username" v-model="param.username"></el-input>
      </el-form-item>
      <el-form-item label="licenseid">
        <el-input placeholder="licenseid" v-model="param.licenseid"></el-input>
      </el-form-item>
      <el-form-item label="experience">
        <el-input placeholder="experience" v-model="param.experience"></el-input>
      </el-form-item>
      <el-form-item label="numDrones">
        <el-input placeholder="numDrones" v-model="param.numDrones"></el-input>
      </el-form-item>
      <el-form-item label="numLocations">
        <el-input placeholder="numLocations" v-model="param.numLocations"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="searchForm">Query</el-button>
      </el-form-item>
    </el-form>
    <el-table
        border
        ref="multipleTable"
        :data="tableList"
        tooltip-effect="dark"
        style="width: 100%"
    >
      <el-table-column
          align="center"
          label="username"
          prop="username"
          sortable
      >
      </el-table-column>
      <el-table-column
          align="center"
          label="licenseid"
          prop="licenseid"
          sortable
      >
      </el-table-column>
      <el-table-column
          align="center"
          label="experience"
          prop="experience"
          sortable
      >
      </el-table-column>
      <el-table-column
          align="center"
          label="numDrones"
          prop="numDrones"
          sortable
      >
      </el-table-column>
      <el-table-column
          align="center"
          label="numLocations"
          prop="numLocations"
          sortable
      >
      </el-table-column>
    </el-table>
    <el-pagination
        background
        layout="prev, pager, next"
        :total="total"
        @current-change="changePage"
    >
    </el-pagination>
  </el-card>
</template>

<script>
export default {
  data() {
    return {
      param: {
        username: '',
        licenseid: '',
        experience: '',
        numDrones: '',
        numLocations: '',
      },
      tableList: [],
      pageInfo: {
        pageNum: 1,
        pageSize: 10,
      },
      total: 0
    }
  },
  mounted() {
    this.getTableList(this.pageInfo)
  },
  methods: {
    getTableList() {
      this.$axios.post('/displayPilotView/findAll', Object.assign(this.param, this.pageInfo)).then(res => {
        this.tableList = res.data.rows;
        this.total = res.data.count;
      })
    },
    searchForm() {
      this.pageInfo.pageNum = 1;
      this.getTableList();
    },
    changePage(e) {
      this.pageInfo.pageNum = e
      this.getTableList()
    }
  },
}
</script>

<style scoped lang="scss">
.el-pagination {
  margin-top: 10px;
}
</style>

